	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import dingdanquxiao from '@/views/dingdanquxiao/list'
	import dingdanpingjia from '@/views/dingdanpingjia/list'
	import dingdanxinxi from '@/views/dingdanxinxi/list'
	import fankuixinxi from '@/views/fankuixinxi/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import dingdanwancheng from '@/views/dingdanwancheng/list'
	import discussdongtaixinxi from '@/views/discussdongtaixinxi/list'
	import dongtaixinxi from '@/views/dongtaixinxi/list'
	import yonghu from '@/views/yonghu/list'
	import chat from '@/views/chat/list'
	import config from '@/views/config/list'
	import yuepaixinxi from '@/views/yuepaixinxi/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/dingdanquxiao',
			name: '',
			component: dingdanquxiao
		}
		,{
			path: '/dingdanpingjia',
			name: '',
			component: dingdanpingjia
		}
		,{
			path: '/dingdanxinxi',
			name: '',
			component: dingdanxinxi
		}
		,{
			path: '/fankuixinxi',
			name: '',
			component: fankuixinxi
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/dingdanwancheng',
			name: '',
			component: dingdanwancheng
		}
		,{
			path: '/discussdongtaixinxi',
			name: '',
			component: discussdongtaixinxi
		}
		,{
			path: '/dongtaixinxi',
			name: '',
			component: dongtaixinxi
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/chat',
			name: '',
			component: chat
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		,{
			path: '/yuepaixinxi',
			name: '',
			component: yuepaixinxi
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
